import { Component, Input, OnInit } from "@angular/core";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ToastrService } from "ngx-toastr";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-videos-lectures",
  templateUrl: "./videos-lectures.component.html",
  styleUrls: ["./videos-lectures.component.scss"],
})
export class VideosLecturesComponent implements OnInit {
  @Input() teacherSubjectId: number = 0;
  @Input() subjectId: number = 0;
  @Input() teacherId: number = 0;

  EduCompList: any = [];
  SubjectName?: string;
  TeacherName?: string;
  unitName?: string;
  lessonName?: string;
  unitId: number;
  isUnit: boolean = true;
  isLesson: boolean;
  isLecture: boolean;
  lessonId: number;

  constructor(
    private AuthServ: AuthService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService
  ) {}

  ngOnInit(): void {
    this.EduCompList = localStorage.getItem("EduCompList");

    if (localStorage.getItem("EduCompList") == null) {
      this.AuthServ.logout();
      this.toastr.info("تم إنتهاء الجلسة قم بتسجيل الدخول مرة اخري");
    }
  }

  getUnitId(event) {
    this.unitId = event;
  }

  checkIsUnit(event) {
    this.isUnit = event;
  }

  checkIsLesson(event) {
    this.isLesson = event;
  }
  checkIsLecture(event) {
    this.isLecture = event;
  }
  getLessonId(event) {
    this.lessonId = event;
  }
  getLessonName(event) {
    this.lessonName = event;
  }
  getUnitName(event) {
    this.unitName = event;
  }
}
