import { forkJoin } from "rxjs";
import { NgxSpinnerService } from "ngx-spinner";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute, Router } from "@angular/router";
import {
  StudentGroupVM,
  IReservationVM,
} from "app/admin/models/admin/reservation";
import { ReservationService } from "app/admin/services/Admin/reservation.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ToastrService } from "ngx-toastr";
import { ConfirmReservationDialogComponent } from "../confirm-reservation-dialog/confirm-reservation-dialog.component";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { StudentService } from "app/student/services/student.service";
import { GenerateCodesService } from "app/admin/services/Admin/generate-codes.service";
import { CheckCodeReservationModel } from "app/student/models/balance";

@Component({
  selector: "app-join-to-group",
  templateUrl: "./join-to-group.component.html",
  styleUrls: ["./join-to-group.component.scss"],
})
export class JoinToGroupComponent implements OnInit {
  code: string;
  viewId: number;
  EduCompId: number;
  currentYear: number;
  teacherId: number;
  NewReservation: number; //(1) for new year. (0) for current year
  EducationTypeId: number; //(1) for Online. (2) for Center
  PaymentGatewayReferenceId: any;
  studyingGroups: StudentGroupVM[] = [];
  Reservations?: IReservationVM;
  myForm: FormGroup;
  balanceForm: FormGroup;
  submitted: boolean = false;
  showGroups: boolean = false;
  isUserHasBalance: boolean = true;
  reservationCompleted: boolean = true;
  updateURL: string = "";
  paymentCode: string = null;

  constructor(
    private fb: FormBuilder,
    private reservationServ: ReservationService,
    private toaster: ToastrService,
    private route: ActivatedRoute,
    public dialog: MatDialog,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
    private studentserv: StudentService,
    private GenerateCodesService: GenerateCodesService
  ) {
    this.route.parent?.paramMap.subscribe((params) => {
      this.code = params.get("code");
      this.viewId = Number(params.get("viewId"));
      this.EduCompId = Number(params.get("EduCompId"));
      this.currentYear = Number(params.get("currentYear"));
      this.teacherId = Number(params.get("teacherId"));
      this.updateURL = `/student/reservation/${this.teacherId}/${this.EduCompId}/${this.viewId}/${this.code}/${this.currentYear}/submit`;
      this.NewReservation = authService.getNewReservation();
    });
  }

  ngOnInit() {
    this.getAvailableGroups();
    this.getReservationInfo();

    //balance Form
    this.balanceForm = this.fb.group({
      teacherId: [this.teacherId, [Validators.required]],
      amount: [100],
      code: ["", [Validators.required]],
    });

    this.myForm = this.fb.group({
      groupId: ["", Validators.required],
      teacherUserId: [this.teacherId, Validators.required],
      eduCompId: [this.EduCompId, Validators.required],
    });
    // this.FormCtrls.teacherUserId.setValue(this.teacherId);
    // this.FormCtrls.eduCompId.setValue(this.EduCompId);

    // this.EducationTypeId = Number(localStorage.getItem("EducationTypeId"));
    // this.EducationTypeId == 1
    //   ? (this.methodId = 3) // fawry
    //   : (this.methodId = 0); // code
    // this.openDialog(' ');
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  get CodeCtrl() {
    return this.balanceForm.get("code");
  }

  ngAfterViewInit() {
    // this.authService.EducationTypeId.subscribe((value) => {
    //   this.EducationTypeId = value;
    //   //online student
    //   this.EducationTypeId == 1 ? (this.methodId = 3) : (this.methodId = 0);
    //   // console.log("EducationTypeId", value);
    //   // console.log("methodId", this.methodId);
    // });
  }

  getAvailableGroups() {
    this.reservationServ
      .getAvailableGroups(
        this.teacherId,
        this.EduCompId,
        this.EducationTypeId == 1 ? true : false
      )
      .subscribe((res: any) => {
        this.studyingGroups = res;

        //new online student add to default online group
        if (this.EducationTypeId == 1) this.FormCtrls.groupId.setValue(125);
      });
  }

  checkUserBalance() {
    this.reservationServ
      .checkUserBalance(this.EduCompId, this.teacherId)
      .subscribe((res: any) => {
        this.isUserHasBalance = res.balance;
        this.reservationCompleted = res.reservationCompleted;
        this.paymentCode = res.paymentCode;

        // new online satudent generat fawry code automatic
        if (
          this.EducationTypeId == 1 &&
          !this.isUserHasBalance &&
          !res.reservationCompleted
        ) {
          let amount = this.balanceForm.value.amount;
          let teacherId = this.balanceForm.value.teacherId;
          this.studentserv
            .payAmount(amount, teacherId, this.EduCompId, 3)
            .subscribe((res: any) => {
              if (res.returnValue == 1) {
                this.toaster.success("تم طلب الكود ستصلك رساله قريباً");
                this.PaymentGatewayReferenceId = res.PaymentGatewayReferenceId;
              } else {
                //this.toaster.error('حدث  خطأ  ما حاول مره اخري لاحقاً');
                this.toaster.error(res.returnString || res.message);
              }
              this.spinner.hide();
            });
        }
      });
  }

  getReservationInfo() {
    this.spinner.show();
    this.reservationServ
      .getReservationInfo(this.EduCompId)
      .subscribe((res: any) => {
        //  console.log('getReservationInfo', res);
        if (res) {
          this.Reservations = res;
          this.EducationTypeId = res.EducationTypeId;
          this.checkUserBalance();
          console.log("EducationTypeId", this.EducationTypeId);
          console.log("NewReservation", this.NewReservation);
        }
        this.spinner.hide();
      });
  }

  // checkCode for new offline student
  checkCodeReservationAmount() {
    if (!this.CodeCtrl.value) {
      this.toaster.warning("يجب ادخال كود شحن صحيح.");
      return;
    } else {
      this.spinner.show();
      this.GenerateCodesService.checkCodeReservationAmount(
        this.CodeCtrl.value
      ).subscribe((res: CheckCodeReservationModel) => {
        //  this.spinner.hide();

        if (res.codePrice == null) {
          this.toaster.warning("يجب ادخال كود صحيح");
          return;
        }

        if (res.codeType?.id != 1) {
          this.toaster.warning("يجب ادخال كود خاص بالحجز");
          return;
        }

        if (res.isEqual && res.codePrice != null) {
          this.applyBalanceCode();
        } else {
          this.toaster.warning(
            `الكود الذي ادخلتة قيمته"${res.codePrice}"، قيمة الشحن المطلوبة "100"`
          );
          return;
        }
      });
    }
    //else this.toaster.error("Please Enter Code Or Serial ");
  }

  applyBalanceCode() {
    this.spinner.show();
    this.GenerateCodesService.applyBalanceCode(this.CodeCtrl.value).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          this.checkUserBalance();
          this.toaster.success("تم شحن الرصيد");
        } else this.toaster.warning(res.returnString);
        this.spinner.hide();
      }
    );
  }

  CompletaReservation() {
    this.submitted = true;
    let valid = this.myForm.valid;
    //console.log("Submit", this.myForm.value);

    if (valid) {
      this.spinner.show();
      this.reservationServ
        .CompletaReservation(this.myForm.value)
        .subscribe((res: any) => {
          this.submitted = false;
          if (res.returnValue == 1 && res.returnString == "Success") {
            this.showGroups = false;
            this.toaster.success("تم تأكيد الحجز الحجز ");
            this.openDialog(res.model);
            this.checkUserBalance();
            this.getReservationInfo();
          } else if (
            res.returnString ==
            "Student already exists in a group with the same teacher and subject"
          ) {
            this.toaster.info("انت بالفعل موجود في مجموعة لهذا المدرس");
          } else if (
            res.returnString ==
            "Not enough credits, go to charge balance page to charge"
          ) {
            this.toaster.info(
              "رصيدك غير كافي اذهب الي شحن الرصيد للشحن واكمال عملية التسجيل"
            );
          } else {
            this.toaster.info(res.returnString);
          }
          this.spinner.hide();
          //  console.log('CompletaReservation', res);
        });
    }
  }

  openDialog(studentData): void {
    const dialogRef = this.dialog.open(ConfirmReservationDialogComponent, {
      data: {
        student: studentData,
        paymentCode: this.paymentCode,
      },

      minHeight: "80vh",
    });
    dialogRef.afterClosed().subscribe((confirm) => {
      if (confirm) true;
    });
  }
}
