import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
@Injectable({
  providedIn: "root",
})
export class SubscriptionService {
  constructor(private http: HttpClient) {}

  getallPaymentPlans() {
    return this.http.get(`${environment.apiURL}/AJAX/getallPaymentPlans`);
  }

  getallPaymentMethod() {
    return this.http.get(`${environment.apiURL}/AJAX/getallPaymentMethod`);
  }

  subscribe(amount: number, planId: number) {
    return this.http.post(
      `${environment.apiURL}AJAX/chargeAmount/${amount}/${planId}`,null
    );
  }
}
