import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import { FileService } from "app/shared/services/General/upload-file.service";
import {
  IChaptersSubjectRevisionsVM,
  IChaptersUnitRevisionsVM,
  ISubjectChaptersVM,
} from "app/student/models/student";
import { TeacherSubjectsService } from "app/student/services/teacherSubjects.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-unit-lectures",
  templateUrl: "./unit-lectures.component.html",
  styleUrls: ["./unit-lectures.component.scss"],
})
export class UnitLecturesComponent implements OnInit {
  currentLang: String;
  constructor(
    private teacherSubServ: TeacherSubjectsService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    public fileService: FileService
  ) {
    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
  }
  @Input("teacherSubjectId") teacherSubjectId = 0;
  @Input("subjectId") subjectId;
  @Input("teacherId") teacherId = 0;

  units: IChaptersUnitRevisionsVM[];
  @Output() isUnit = new EventEmitter<boolean>();
  @Output() unitId = new EventEmitter<number>();
  @Output() isLesson = new EventEmitter<boolean>();
  showLectureSubjects: boolean = false;
  subjectRevisions: IChaptersSubjectRevisionsVM;
  subjectName: string;

  teacherName: string;
  ngOnInit(): void {
    this.getUnits();
  }
  //Get Unit
  getUnits() {
    this.spinner.show();
    this.teacherSubServ
      .ReturnUnitListBySubjectId(this.subjectId)
      .subscribe((res: ISubjectChaptersVM) => {
        this.units = res?.unitRevisions;
        this.subjectName = res?.subject_name;
        this.subjectRevisions = res?.subject;
        this.teacherName = res?.teacher_name;
        this.spinner.hide();
      });
  }
  //Get Show lesson and hide unit

  showLesson(unitId: number) {
    this.isUnit.emit(false);
    this.isLesson.emit(true);
    this.unitId.emit(unitId);
  }

  imageUrl(path: string) {
    let imagePath = "";

    if (path != null) {
      imagePath = this.fileService.previewPicture(path);
    } /* else if (this.teacherId == 22) {
      imagePath = "assets/img/licpic.jpeg";
    } */ else {
      imagePath = "assets/img/lectureBackground.jpeg";
    }
    return imagePath;
  }
}
