import { Component, OnInit, ViewChild } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";

import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { SubscriptionService } from "app/student/services/subscription.service";
import { LanguageService } from "app/shared/services/language.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { StudentService } from "app/student/services/student.service";
import { IPaymentPlans, IStudentSubscriptions } from "app/student/models/subscription";
@Component({
	selector: "app-student-subscription",
	templateUrl: "./student-subscription.component.html",
	styleUrls: ["./student-subscription.component.scss"],
})
export class StudentSubscriptionComponent implements OnInit {
	myForm: FormGroup;
	paymentPlans: IPaymentPlans[];
	constructor(
		private subscriptionService: SubscriptionService,
		public languageService: LanguageService,
		private toaster: MessagesService,
		private spinner: NgxSpinnerService
	) {}

	ngOnInit(): void {
		this.getallPaymentPlans();
	}

	getallPaymentPlans() {
		this.subscriptionService.getallPaymentPlans().subscribe((res: any) => {
			this.paymentPlans = res.paymentPlans;
		});
	}

	subscribe(paymentPlan: IPaymentPlans) {
		this.spinner.show();
		this.subscriptionService.subscribe(paymentPlan.price, paymentPlan.id).subscribe((res: any) => {
			if (res.returnValue == 1) {
				window.open(res.returnURL, "target_blank");
			} else {
				this.toaster.warning(res.returnString ?? res.message);
			}
			this.spinner.hide();
		});
	}
}
