import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { lesson, unit } from "app/admin/models/admin/educations";
import { TeacherSubjectsService } from "app/student/services/teacherSubjects.service";
import { TranslateService } from "@ngx-translate/core";
import { FileService } from "app/shared/services/General/upload-file.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-lesson-lectures",
  templateUrl: "./lesson-lectures.component.html",
  styleUrls: ["./lesson-lectures.component.scss"],
})
export class LessonLecturesComponent implements OnInit {
  currentLang: String;

  constructor(
    private TeacherSubServ: TeacherSubjectsService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    public fileService: FileService
  ) {
    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
  }
  lessons: lesson[];
  unit: unit;
  @Input() subjectId: number = 0;
  @Input() unitId: number;
  @Input() teacherSubjectId: number = 0;
  @Input() teacherId: number = 0;
  @Output() unitName = new EventEmitter<string>();
  @Output() isUnit = new EventEmitter<boolean>();
  @Output() lessonId = new EventEmitter<number>();
  @Output() lessonName = new EventEmitter<string>();
  @Output() isLesson = new EventEmitter<boolean>();
  @Output() isLecture = new EventEmitter<boolean>();
  showLectureunits: boolean = false;
  subjectName: string;

  teacherName: string;
  ngOnInit(): void {
    this.getLessons();
  }

  getLessons() {
    this.spinner.show();
    this.spinner.show();
    this.TeacherSubServ.ReturnLessonListBySubjectId(
      this.unitId,
      this.subjectId
    ).subscribe((res: any) => {
      this.lessons = res.lessons;
      this.unit = res.unit;
      this.subjectName = res.subject_name;
      this.teacherName = res.teacher_name;
      this.spinner.show();
    });
  }
  //to back to unit
  ToBack() {
    this.isLesson.emit(false);
    this.isUnit.emit(true);
  }
  //Show lecture  and hide lesson

  showLecture(lesson: lesson) {
    this.isLesson.emit(false);
    this.isLecture.emit(true);
    this.lessonId.emit(lesson.id);
    this.lessonName.emit(lesson.lesson_ar_name);
    this.unitName.emit(this.unit.unit_ar_name);
  }
  imageUrl(path: string) {
    let imagePath = "";

    if (path != null) {
      imagePath = this.fileService.previewPicture(path);
    } /* else if (this.teacherId == 22) {
      imagePath = "assets/img/licpic.jpeg";
    } */ else {
      imagePath = "assets/img/lectureBackground.jpeg";
    }
    return imagePath;
  }
}
