import { IMaterialVM } from "app/admin/models/admin/educations";
import { StudentService } from "app/student/services/student.service";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { TeacherSubjectsService } from "app/student/services/teacherSubjects.service";
import {
	IFileDocumentVM,
	ITeacherDataPostVM,
	ITeacherDataVM,
	ITeacherSubjectFilesVM,
	ITopExamStudentsVM,
	ITopStudentsVM,
} from "app/student/models/student";
import { IZoomDataModel, TeacherAnnouncementVM } from "app/student/models/ask-teacher";
import { TeacherAnnouncementComponent } from "../teacher-announcement/teacher-announcement.component";
import { MatDialog } from "@angular/material/dialog";
import { AnnouncementService } from "app/admin/services/Admin/announcement.service";
import { ZoomService } from "app/shared/services/zoom.service";
import { HelpingRequestService } from "app/student/services/helping-request.service";
import { SweetAlertIcon } from "app/shared/data/sweet-alert";
import { SweetAlertService } from "app/shared/services/sweet-alert.service";
import { NgxSpinnerService } from "ngx-spinner";
import { FileService } from "app/shared/services/General/upload-file.service";

@Component({
	selector: "app-teacher-subject-profile",
	templateUrl: "./teacher-subject-profile.component.html",
	styleUrls: ["./teacher-subject-profile.component.scss"],
})
export class TeacherSubjectProfileComponent implements OnInit {
	TeacherData?: ITeacherDataVM;
	TopStudentsList: ITopStudentsVM[] = [];
	TopExamStudentsList: ITopExamStudentsVM[] = [];
	FilesList?: IMaterialVM[] = [];
	teacherId: number;
	subjectId: number;
	teacherSubjectId: number;
	EduCompId: number;
	showBoxes: boolean = true;
	showChat: boolean = false;
	showHelpfore: boolean = false;
	teacherAnnounce: TeacherAnnouncementVM[] = [];

	meetingNumber: string;
	passWord: string;
	zoomSdkKey?: string;
	zoomSdkSecret?: string;

	// zoomData: IZoomDataModel[] = [
	//   {
	//     meetingNumber: "5928232148",
	//     passWord: "FXZ7QB",
	//     zoomSdkKey: "cMSig6jnR3m5KyLoc7NEw",
	//     zoomSdkSecret: "vDDA2W9XQo8GVMxgTZUiq4RTd1EYYV6f",
	//   },
	// ];

	constructor(
		private TeacherSubServ: TeacherSubjectsService,
		private route: ActivatedRoute,
		private dialog: MatDialog,
		public fileService: FileService,
		private StudentService: StudentService,
		private _helpingRequestService: HelpingRequestService,
		private spinner: NgxSpinnerService,
		private sweetAlertService: SweetAlertService,
		private announcementService: AnnouncementService //	private zoomService: ZoomService
	) {
		this.route.paramMap.subscribe((params) => {
			this.teacherId = Number(params.get("teacherId"));
			this.subjectId = Number(params.get("subjectId"));
			this.teacherSubjectId = Number(params.get("teacherSubjectId"));
		});
	}

	ngOnInit(): void {
		this.ReturnTeacherData(this.subjectId, this.teacherId);
		this.ReturneFilesList();
		// this.getZoomMeetingSignature();
		//	this.ReturnTeacherAnnouncement(this.teacherId);
	}

	ReturnTeacherData(subject_id: any, teacher_ID: any) {
		let data: ITeacherDataPostVM = {
			subject_id: subject_id,
			teacherUserId: teacher_ID,
		};
		this.TeacherSubServ.ReturnTeacherData<ITeacherDataVM, ITeacherDataPostVM>(data).subscribe(
			(res) => {
				this.TeacherData = res[0];
				this.EduCompId = res[0]?.EduCompId;
				if (this.teacherId == 22) {
					this.GetTopStudents(res[0].EduCompId, this.teacherId, this.subjectId);
					this.GetTopExamStudents(res[0].EduCompId, this.teacherId);
				}
			}
		);
	}

	ReturneFilesList() {
		let data: ITeacherSubjectFilesVM = {
			//teacher: this.teacherId,
			teacherUserId: this.teacherId,
			subject: this.subjectId,
			teacher_subject: this.teacherSubjectId,
		};
		this.TeacherSubServ.ReturneFilesList<IFileDocumentVM, ITeacherSubjectFilesVM>(data).subscribe(
			(res) => {
				//console.log("List", res);
				this.FilesList = res.materialsList;
			}
		);
	}

	GetTopStudents(EduCompId: any, TeacherId: any, SubjectId: any) {
		this.StudentService.GetTopStudents<ITopStudentsVM[]>(EduCompId, TeacherId, SubjectId).subscribe(
			(res) => {
				this.TopStudentsList = res;
			}
		);
	}

	GetTopExamStudents(EduCompId: any, TeacherId: any) {
		this.StudentService.GetTopExamStudents<ITopExamStudentsVM[]>(EduCompId, TeacherId).subscribe(
			(res) => {
				this.TopExamStudentsList = res;
			}
		);
	}

	ReturnTeacherAnnouncement(teacher_ID: number) {
		this.announcementService
			.getAnnouncements<TeacherAnnouncementVM[]>(this.teacherId)
			.subscribe((res) => {
				if (res) {
					if (res.length > 0) {
						this.teacherAnnounce = res;
						// console.log(this.teacherAnnounce);
						this.openAnnouncementDialog(this.teacherAnnounce);
					}
				}
			});
	}

	openAnnouncementDialog(item: TeacherAnnouncementVM[]): void {
		this.dialog
			.open(TeacherAnnouncementComponent, {
				data: {
					msg: item,
				},
			})
			.afterClosed()
			.subscribe((confirm) => {
				if (confirm) console.log("dialog");
			});
	}

	contactRequest() {
		this.spinner.show();
		this._helpingRequestService.contactRequest(this.teacherSubjectId).subscribe((res: any) => {
			console.log("contactRequest", res);
			if (res.returValue == 1) {
				this.sweetAlertService.SweetAlert({
					title: "labels.AskForHelp",
					icon: SweetAlertIcon.success,
					describtion:
						"تم إرسال طلب المساعدة الي المدرس، سوف يتم ارسال رسالة عبر واتساب الخاص بك حينما يستقبل المدرس طلبك",
				});
			}
			this.spinner.hide();
		});
	}

	// getZoomMeetingSignature() {
	// 	this.zoomService
	// 		.getZoomDataForTeacher(this.teacherSubjectId)
	// 		.subscribe((res: IZoomDataModel) => {
	// 			this.meetingNumber = res.meetingNumber;
	// 			this.passWord = res.passWord;
	// 			this.zoomSdkKey = res.zoomSdkKey;
	// 			this.zoomSdkSecret = res.zoomSdkSecret;
	// 		});
	// }
}
