import { SubjectsService } from "app/admin/services/Admin/subjects.service";
import { LanguageService } from "app/shared/services/language.service";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-subject-lectures",
  templateUrl: "./subject-lectures.component.html",
  styleUrls: ["./subject-lectures.component.scss"],
})
export class SubjectLecturesComponent implements OnInit {
  subjectId: number = 0;
  yearId: number;
  subject: any;

  constructor(
    private subjectsService: SubjectsService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    public languageService: LanguageService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.yearId = Number(params.get("customYearId"));
      this.subjectId = Number(params.get("subjectId"));
      if (this.subjectId > 0) {
        this.getSubjectData();
      }
    });
  }

  ngOnInit(): void {}

  getSubjectData() {
    this.subjectsService
      .getSubjectByID(this.subjectId)
      .subscribe((res: any) => {
        if (res.subject_id != null) {
          this.subject = res;
        }
      });
  }
}
